import * as User from '../../apis/user'
import { isEmpty } from '../../utils'

export function login (args) {
  if (isEmpty(args)) {
    return Promise.reject(new Error('用户名密码不能为空'))
  } else {
    return User.login(args)
  }
}

export function register (args) {
  User.register(args)
  return '注册成功'
}