<template>
  <div class="hello">
    <img class="head_icon"
         :src="userInfo.headIcon"
         :alt="userInfo.nickname">
    <p>{{userInfo.nickname}}</p>
    <p>{{userInfo.phonenum}}</p>
    <p>{{userInfo.email}}</p>
    <p>{{userInfo.gender}}</p>
  </div>
</template>

<script>
import qs from 'querystring'
import { user } from '../../../plugins-3d'
// import { isEmpty } from '../../../plugins-3d/src/utils'
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data () {
    return {
      userInfo: {}
    }
  },
  async mounted () {
    console.log('>>>>>user: ', user)
    await user.login(qs.stringify({
      username: 'ls',
      password: '123123'
    })).then(({ data }) => {
      if (data.status === 200) {
        this.userInfo = data.data
        console.log('登录成功；', JSON.stringify(data.data, null, 2))
      } else {
        console.log('登录失败：', data.message)
      }
    }).catch(err => {
      console.log('登录失败：', err.message)
    })
  }
}
</script>

